    class Reader extends React.Component {
        constructor(props) {
            super(props);
        }
        
    // componentDidMount() { 
    //      getVisibleIndex() 
    //     }

        render() {
            return (
                <Panel id={this.props.id}>
                <PanelHeader 
         left={<HeaderButton onClick={() => this.props.this.goBack()}><Icon24BrowserBack/></HeaderButton>}
         >
             Назад
         </PanelHeader>

                        <div className="hard">
            <div className="text">
                <div dangerouslySetInnerHTML={{__html: this.props.this.state.booktext}} />
            </div>
            </div>
            {this.props.this.state.is_rated === false ? 
            <Div>
                <Button  size="xl" level="secondary" style={{backgroundColor: "#00cbbe", color: "#ffffff"}} stretched onClick={() => this.props.this.setState({activeModal: "modals", commentforrezention_add: ""})}>Оценить</Button>
            </Div>    
                :null}
            </Panel>
            )
            }
        }
  
export default Reader;